* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  white-space: pre;
}
nav {
  background: rgb(12, 46, 36) !important;
}
.nav a div {
  color: white !important;
}
.detail {
  height: 90px;
  padding: 5px;
  position: relative;
}
.detail p {
  font-size: 18px;
}

.detail cite {
  display: block;
  position: absolute;
  bottom: 10px;
  left: 10px;
}
.card .card-image img {
  height: 360px;
}
a {
  color: rgb(37, 36, 36);
}
.deatilContainer {
  padding: 20px 0;
}

@media screen and (max-width: 601px) {
  .card .card-image img {
    height: 600px !important;
  }
}
@media screen and (max-width: 490px) {
  .card .card-image img {
    height: 500px !important;
  }
}
@media screen and (max-width: 380px) {
  .card .card-image img {
    height: 400px !important;
  }
}
@media screen and (max-width: 340px) {
  .card .card-image img {
    height: 350px !important;
  }
}
